"use client";
import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

// Icon mapping for consistent icons
const ICONS = {
  dashboard: 'üè†',
  car: 'üöó',
  management: 'üìã',
  parking: 'üÖøÔ∏è',
  complaint: 'üì¢',
  report: 'üìä',
  service: 'üõ†Ô∏è',
  field: 'üìç',
  order: 'üì¶',
  assign: 'üë•',
  attendance: '‚úÖ',
  view: 'üëÄ',
  generate: 'üìà'
};

interface SidebarItem {
  title: string;
  link: string;
  icon: string;
  subItems?: {
    title: string;
    link: string;
    icon?: string;
  }[];
}

export default function Sidebar({ items, isOpen, onToggle }: { 
  items: SidebarItem[];
  isOpen: boolean;
  onToggle: () => void;
}) {
  const [expandedItems, setExpandedItems] = useState<Record<string, boolean>>({});

  const toggleItem = (link: string) => {
    setExpandedItems(prev => ({
      // Collapse all other items
      ...Object.fromEntries(Object.keys(prev).map(key => [key, false])),
      // Toggle current item
      [link]: !prev[link]
    }));
  };

  return (
    <div className={`bg-gray-800 text-white ${isOpen ? 'w-64' : 'w-20'} min-h-screen transition-all duration-300`}>
      <div className="p-4 flex justify-between items-center border-b border-gray-700">
        {isOpen && <h2 className="text-xl font-bold">TMS</h2>}
        <button 
          onClick={onToggle}
          className="p-2 rounded-lg hover:bg-gray-700"
          aria-label={isOpen ? 'Collapse sidebar' : 'Expand sidebar'}
        >
          {isOpen ? '¬´' : '¬ª'}
        </button>
      </div>
      <nav className="mt-6">
        <ul>
          {items.map((item) => (
            <SidebarItem 
              key={item.link}
              item={item}
              isOpen={isOpen}
              isExpanded={!!expandedItems[item.link]}
              onToggle={() => toggleItem(item.link)}
            />
          ))}
        </ul>
      </nav>
    </div>
  );
}

function SidebarItem({ item, isOpen, isExpanded, onToggle }: { 
  item: SidebarItem;
  isOpen: boolean;
  isExpanded: boolean;
  onToggle: () => void;
}) {
  const pathname = usePathname();
  const isActive = pathname.startsWith(item.link);

  return (
    <li className="mb-2">
      {item.subItems ? (
        <>
          <div 
            className={`flex items-center justify-between p-3 ${isActive ? 'bg-gray-700' : 'hover:bg-gray-700'} rounded-lg cursor-pointer`}
            onClick={onToggle}
          >
            <div className="flex items-center">
              <span className="mr-3">{item.icon}</span>
              {isOpen && <span>{item.title}</span>}
            </div>
            {isOpen && item.subItems && (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className={`h-4 w-4 transition-transform ${isExpanded ? 'rotate-180' : ''}`}
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            )}
          </div>
          {isOpen && isExpanded && item.subItems && (
            <ul className="ml-6 mt-2 space-y-1">
              {item.subItems.map((subItem) => (
                <li key={subItem.link}>
                  <Link
                    href={subItem.link}
                    className={`flex items-center p-2 pl-8 ${pathname === subItem.link ? 'text-blue-400' : 'hover:text-gray-300'}`}
                  >
                    <span className="mr-3">{subItem.icon || ICONS[subItem.title.toLowerCase().replace(' ', '_')] || '‚Ä¢'}</span>
                    {subItem.title}
                  </Link>
                </li>
              ))}
            </ul>
          )}
        </>
      ) : (
        <Link
          href={item.link}
          className={`flex items-center p-3 ${pathname === item.link ? 'bg-blue-700' : 'hover:bg-gray-700'} rounded-lg`}
        >
          <span className="mr-3">{item.icon}</span>
          {isOpen && <span>{item.title}</span>}
        </Link>
      )}
    </li>
  );
}